@use "./variables" as *;

@mixin Phone {
  @media screen and (max-widht: 430px) {
    @content;
  }
}

@mixin Tablet {
  @media screen and (min-width: 768px) {
    @content;
  }
}

@mixin Desktop {
  @media screen and (min-width: 1280px) {
    @content;
  }
}

@mixin Button {
  height: 45px;
  width: fit-content;
  padding: 10px 20px;
  background-color: $primary-color;
  cursor: pointer;
  font-size: 1rem;
  border: none;
}
