@use "../../Styles/variables" as *;
@use "../../Styles/mixins" as *;

.landing {
  background-color: $primary-background;
  width: 100%;
  min-height: 100dvh;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.hero {
  display: flex;
  justify-content: space-between;
  align-items: center;
  min-height: 500px;
  height: 100dvh;
  width: 100%;
  max-width: 1280px;
  padding: 20px;

  @include Tablet {
    min-height: 100dvh;
    height: 100%;
    padding: 90px 50px 0 50px;
  }

  &__title {
    color: $white;
    font-size: clamp(2rem, 5vw + 1rem, 3rem);
    font-weight: 400;
    line-height: 1.2;
  }

  &__text {
    color: $white;
  }

  &__container {
    display: flex;
    flex-direction: column;
    gap: 40px;

    max-width: 500px;

    @include Tablet {
      gap: 60px;
      width: 50%;
    }
  }

  &__button {
    @include Button;
    height: 60px;
    font-size: 20px;
    @include Tablet {
      font-size: 2rem;
    }
  }

  &__img {
    display: none;
    @include Tablet {
      display: flex;
      width: 40%;
      max-width: 500px;
    }
  }
}
